/*Button*/

@mixin color($color) {
    background-color: $color;
    border: 2px solid $color;
    &:hover {
        background-color: white;
        color: $color;
    }
}

.btn {
    color: white;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    letter-spacing: .08rem;
    margin: 1.8rem .8rem;
    padding: 1rem 2rem;
    transition: all .4s;
    text-transform: uppercase;
    &--orange {
        @include color(#F79E02);
    }
    &--cyan {
        @include color(#5CDB95);
    }
    &--blue {
        @include color(#97CAEF);
    }
    &--pink {
        @include color(#F64C72);
    }
    &--black {
        @include color(#112D32);
    }
    &--red {
        @include color(#C60021);
    }
    &--gray {
        @include color(#CDCDCD);
    }
}

#participantButton,
#partnerButton,
#volunteerButton {
    color: black;
    font-size: 20px;
    width: 200px;
    height: 40px;
    padding-top: 10px;
    margin: 40px;
    border-radius: 25px;
    background-color: aqua;
    text-align: center;
}

#participantButton:hover,
#partnerButton:hover,
#volunteerButton:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    transition: all 300ms;
}

/*Accordion*/

.accordion {
    margin: 0 auto;
}

.accordion ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.accordion li {
    margin: 0;
    padding: 25px;
}

.accordion [type=radio],
.accordion [type=checkbox] {
    display: none;
}

.accordion label {
    display: block;
    font-size: 16px;
    line-height: 16px;
    background-color: #a9a9a9;
    color: black;
    font-weight: 700;
    cursor: pointer;
    text-transform: uppercase;
}

.accordion ul li label:hover,
.accordion [type=radio]:checked~label,
.accordion [type=checkbox]:checked~label {
    color: white;
}

.accordion .content {
    padding: 0 10px;
    overflow: hidden;
}

.accordion .text {
    padding: 15px 10px;
}

.accordion p:hover {
    color: red;
    cursor: pointer;
    background-color: #35e2df;
}

.accordion h3 {
    padding: 0;
    margin: 10px 0;
}

.vertical ul li {
    overflow: hidden;
    margin: 0 0 1px;
}

.vertical ul li label {
    padding: 10px;
}

.vertical [type=radio]:checked~label,
.vertical [type=checkbox]:checked~label {
    border-bottom: 0;
}

.vertical ul li label:hover {
    background-color: #d9d9d9;
}

.vertical ul li .content {
    height: 0px;
    border-top: 0;
    transition: all .5s ease-out;
}

.vertical [type=checkbox]:checked~label~.content {
    height: 100%;
}

@media(max-width: 800px) {
    
    .accordion li {
        padding: 5px;
    }
}

