/*rotating animation for the home page logo*/
.rotate-center {
	-webkit-animation: rotate-center 0.8s ease-in-out both;
	        animation: rotate-center 0.8s ease-in-out both;
}

 @-webkit-keyframes rotate-center {
    0% {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
    }
  }
  @keyframes rotate-center {
    0% {
      -webkit-transform: rotate(0);
              transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
    }
  }



/*Intro animations*/
.fade-in-left {
	-webkit-animation: fade-in-left 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in-left 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes fade-in-left {
    0% {
      -webkit-transform: translateX(-50px);
              transform: translateX(-50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
  @keyframes fade-in-left {
    0% {
      -webkit-transform: translateX(-50px);
              transform: translateX(-50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }


.fade-in-right {
	-webkit-animation: fade-in-right 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: fade-in-right 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes fade-in-right {
    0% {
      -webkit-transform: translateX(50px);
              transform: translateX(50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
  @keyframes fade-in-right {
    0% {
      -webkit-transform: translateX(50px);
              transform: translateX(50px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
      opacity: 1;
    }
  }
/*Intro*/

.containerIntro-ourStory {
    background-image: linear-gradient(
                     rgba(0, 0, 0, .6), 
                     rgba(0, 0, 0, .6)),
                     url(/static/media/individual.58c997d2.jpg);
}

/*intro*/
.containerIntro-calendar {
    background-image: linear-gradient(
                     rgba(0, 0, 0, .6), 
                     rgba(0, 0, 0, .6)),
                     url(/static/media/calendarBackgroundPhoto.de9e2f17.jpg);
}

.containerMain-upcomingEvents {
    display: flex;
    flex-direction: column;
    background-color: white;
    color: black;
    overflow: hidden;
}

.flexContainer-upcomingEvents {
    display: flex; 
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    padding-left: 11rem;
    padding-right: 9rem;
    padding-top: 6rem;
    padding-bottom: 6rem;
}

.flexItem-left-upcomingEvents {
    max-width: 20rem;
    text-align: left;
    padding: 1rem;
    color: black;
}

.flexItem-right-upcomingEvents {
    display: flex;
    flex-direction: column;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    padding: 1rem;
}

@media(max-width: 800px) {

    .containerMain-upcomingEvents {
        align-items: center;
        align-content: center;
    }

    .flexContainer-upcomingEvents {
        align-items: center;
        flex-direction: column;
    }
    
    .flexItem-left-upcomingEvents {
        text-align: center;
        max-width: 15rem;
        padding-right: 3rem;
    }

    .flexItem-right-upcomingEvents {
        overflow: hidden;
        padding-left: 20rem;
        padding-right: 0rem;
        padding-top: 1rem;
        padding-bottom: 1rem;
        max-width: 60rem;
    }
}

