.email {
    font-size: inherit;
}

@media(max-width: 768px) {
    .avatar {
        object-fit: cover;
        object-position: 100% 0;
        border-radius: 50%;
        width: 120px;
        height: 120px;
        display: inline-block;
        opacity: 1;
        margin-top: 40px;
    }

